.daysLeft {
  padding-left: 10px; }
.daysRight {
  padding-right: 10px; }

.daysContentItem {
  font-size: 12px;
  border-top: 1px solid white;
  display: table-cell;
  width: 28px;
  height: 28px; }
.daysContentItemNotEmpty {
  &:hover {
    background-color: #FFE082;
    font-weight: 600;
    cursor: pointer; } }
.daysContentItemSelected {
  background-color: #72C4FF;
  color: #1A237E;
  font-weight: 600; }

.cellContent {
  height: 100%;
  vertical-align: middle;
  display: flex;
  justify-content: flex-end;
  align-items: center; }
.daysContentItemNotCurrentMount {
  color: #212121;
  opacity: 0.5; }
.daysDayRangeFrom {
  background: url("./img/svg/selected-from.svg") no-repeat left;
  background-size: cover; }
.daysDayRangeTo {
  background: url("./img/svg/selected-to.svg") no-repeat right;
  background-size: cover; }
.daysDayRangeOnDay {
  background-color: #29B6F6; }
.daysDayBetween {
  background-color: #81D4FA; }

.daysDayRangeIfNotCurrentMount {
  background-color: #29B6F6;
  color: #424242; }
.daysDayBetweenIfNotCurrentMount {
  background-color: #81D4FA;
  color: #424242; }


.daysBodyBox {
  display: table;
  margin-bottom: 7px;
  .daysColumnBox {
    display: table-header-group;
    .daysColumnItem {
      font-size: 12px;
      display: table-cell;
      padding: 0 0 10px; }
    .daysWeekend {
      color: #d50000; } }
  .daysContentBox {
    display: table-row-group;
    .daysContentRow {
      display: table-row;
      &:hover {
        background-color: #ECEFF1; } } } }

.daysContentItemDisable {
  color: #BDBDBD;
  &:hover {
    cursor: pointer;
    background-color: #ffcdd2; } }


.cellContentText {
  margin-right: 8px; }

.columnCellContentText {
  align-items: center;
  text-align: center; }



