.textfieldBox {
  display: flex;
  align-items: center;
  position: relative;
  .textfieldInput {
    width: 100%;
    padding: 4px 0 4px 7px;
    font-size: 14px;
    border: 1px solid #72C4FF; }
  .textfieldInputEmpty {
    color: #9E9E9E; }
  .textfieldIconBox {
    display: flex;
    align-items: center;
    position: absolute;
    right: 6px; } }



