.nextbackBox {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 7px 0 7px 0;
  .nextbackHeader {
    display: flex;
    align-items: center;
    font-size: 15px;
    height: 20px;
    >*+* {
      margin-left: 5px; } }
  .nextbackGoLeftRightIcon {
    height: 24px;
    width: 24px;
    color: #757575;
    &:hover {
      color: #424242;
      cursor: pointer; } } }

.nextbackHeaderStyle {
  background-color: #F7F8FA; }



