.uikit {
  background-color: #FAFAFA;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  > *+* {
    margin-top: 10px; }
  .uikitBox {
    box-shadow: 0 0 3px rgba(0,0,0,0.1);
    max-width: 500px;
    width: 500px;
    .uikitTitle {
      font-weight: bold;
      font-size: 17px;
      margin: 10px 0 10px 10px; }
    .uikitLabel {
      padding: 5px;
      margin-bottom: 5px;
      word-wrap: break-word; }
    .uikitParams {
      padding: 5px;
      > *+* {
        margin-top: 7px; } }
    .uikitDemo {
      max-width: 260px;
      background-color: #9E9E9E;
      padding: 5px;
      margin-bottom: 5px;
      min-height: 250px;
      max-height: 250px; } } }


.boxParams {
  display: flex;
  flex-direction: column;
  > *+* {
    margin-left: 5px; }
  .boxParamsPropName {
    font-weight: bold;
    background-color: rgba(236,239,241 ,1);
    padding: 1px 5px 1px;
    box-shadow: 0 0 3px rgba(0,0,0,0.1); }
  .boxParamsPropType {
    color: #757575; }
  .boxParamsPropDefValue {
    color: #757575; }
  .boxParamsPropDescription {
    font-size: 17px; } }

.flex5 {
  display: flex;
  > *+* {
    margin-left: 5px; } }



