.tmcBox {
  min-width: 200px; }

.tmcTitle {
  padding: 3px;
  color: #1565C0; }

.tableBox {
  .table {
    padding: 5px;
    display: flex;
    flex-direction: column; }
  .row {
    display: flex;
    justify-content: space-around; }
  .cell {
    font-size: 14px;
    display: flex;
    padding: 5px;
    &:hover {
      cursor: pointer;
      background-color: #FFE082; } }
  .cellSelected {
    background-color: #4FC3F7; } }

.tmcTitleBack {
  width: 24px;
  height: 24px;
  &:hover {
    cursor: pointer; } }

.label300 {
  color: #9E9E9E; }

.label400 {
  color: #757575; }



