.tmBox {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  > *+* {
    margin-left: 5px; }
  .tmInputBox {
    display: flex;
    align-items: center;
    position: relative;
    .tmInput {
      width: 75px;
      height: 20px;
      border: 1px solid #9E9E9E;
      padding: 2px 2px 2px 7px;
      font-size: 14px; }
    .tmInputIconBox {
      position: absolute;
      right: 6px;
      .tmInputIcon {
        height: 12px; } } }
  .tmButton {
    margin-left: 10px;
    font-size: 14px;
    flex-grow: 1;
    text-align: center;
    background-color: #42A5F5;
    color: white;
    padding: 5px 0 4px;
    &:hover {
      cursor: pointer; } }
  .tmButtonDisable {
    border: 1px solid #72C4FF;
    background-color: white;
    color: #72C4FF;
    &:hover {
      cursor: default; } } }

.btmIcon {
  &:hover {
    cursor: pointer; } }

.inputError {
  background-color: #ffebee; }



