.rootInputBox {
  font-family: Roboto, serif;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  > *+* {
    margin-left: 5px; }
  .rootInput {
    flex-grow: 1; } }

.rootIcon {
  color: #42A5F5;
  height: 12px;
  &:hover {
    color: #1976D2;
    cursor: pointer; } }
.rootIconIfOpen {
  color: #1976D2; }

.rootCalendar {
  position: absolute;
  background-color: white; }



